import React from 'react'
import './slide.styles.css'
import ConnectBtn from '../../../../../components/ConnectBtn';
import Count from '../../../../../components/Count';
import Social from '../../../../../components/Social';
import Infinity from '../../../../../assets/icons/Infinity.svg';
import ButtonTrans from '../../../../../components/ButtonTrans';
import ButtonWhite from '../../../../../components/ButtonWhite';


export const Slide = ({ headline, body, ctaText, ctaLink, mediaType, src, videoRef, poster }) => {

  return (
    <>
      <div className="slide-block-background">
          <video ref={videoRef} width="100%" muted autoplay="" loop={true} poster={poster} className="slide-block-background-img">
            <source src={src} type="video/mp4">
            </source>
          </video>
        <div className="slide-block-overlay"></div>
      </div>

      <section className="slide-block-text pt-40 pb-32 container mx-auto relative lg:pt-50 lg:h-screen lg:pb-0 md:pl-12 sm:pl-4 sm:pt-20 sm:h-screen md:text-center lg:w-full">
        <h1 className="text-white text-40pix mt-16 md:mt-0 font-bold uppercase w-10/12 tracking-wide lg:w-9/12 md:w-10/12 sm:w-full sm:text-2r akira">
        The First mmorpg built on the <span className="text-merah-muda">avalanche</span> blockchain
        </h1>
        <p className="text-abu-tag-cover capitalize text-sm py-11 w-35prc md:w-6/12 sm:w-full sm:py-4">
          A Space Exploration Metaverse With Endless Environments To Explore. Avalanche First Meta-Multiverse
        </p>
        <div className="flex items-center flex-wrap gap-4 lg:justify-center">
          <ButtonWhite/>
          <ButtonTrans/> 
          <Social
            isHeader
            className="grid social-header"
          />
        </div>

        <div className="flex items-center md:justify-center gap-4 mt-8">
          <p className="text-sm text-white font-semibold">Incubated by </p>
          <img src={Infinity} alt="" />
        </div>
      </section>
    </>
  )

}