import React, { useEffect, useState } from 'react';
import Unity, { UnityContent } from 'react-unity-webgl';

const unityContext = new UnityContent(
  'unityBuild/Build/WEB.json',
  'unityBuild/Build/UnityLoader.js'
);

const WebGL = () => {
  const [progression, setProgression] = useState(0);

  useEffect(function () {
    unityContext.on('progress', function (progression) {
      setProgression((progression * 100).toFixed(0));
    });
  }, []);
  return (
    <div className="webgl-content relative w-full h-full grid place-items-center">
      <span
        className={`text-white absolute z-50 text-5xl ${
          Number(progression) >= 98 ? 'opacity-0' : 'opacity-100'
        } transition-all`}
      >
        {progression}%
      </span>
      <Unity unityContent={unityContext} />
    </div>
  );
};

export default WebGL;
