import React, { useState, useEffect } from 'react';
import Logo from '../../components/Logo';
import { Button } from './Button';
import { Link } from 'react-router-dom';
import './index.css';

const Navbar = () => {
    const [click, setClick] = useState(false);
    const [button, setButton] = useState(true);
    const [navbar, setNavbar] = useState(false);

    const handleClick = () => setClick(!click);
    const closeMobileMenu = () => setClick(false);

    const showButton = () => {
      if (window.innerWidth <= 960) {
        setButton(false);
      } else {
        setButton(true);
      }
    };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener('resize', showButton);

  const changeBackground = () => {
    if(window.scrollY >= 80) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  }

  window.addEventListener('scroll', changeBackground);

  return (
    <>
      <nav className={navbar ? 'navbar active' : 'navbar'}>
        <div className='container flex justify-between items-center py-5 mx-auto'>
          <Logo />
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Home
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/marketplace'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Marketplace
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/features'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Features
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/tokenomics'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Tokenomics
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/team'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Team
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/investors'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Investors
              </Link>
            </li>
            <li className='nav-item'>
              <Link
                to='/roadmap'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Roadmap
              </Link>
            </li>

            <li className='nav-item'>
              <Link
                to='/contact'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Contact Us
              </Link>
            </li>

            <li>
              <Link
                to='/'
                className='nav-links-mobile-cus'
                onClick={() => alert('Coming Soon')}
              >
                Join our Discord
              </Link>
            </li>

            <li>
              <Link
                to='/'
                className='nav-links-mobile'
                onClick={() => alert('Coming Soon')}
              >
                Connect Wallet
              </Link>
            </li>
          </ul>
          <a
            to="/"
            className="cursor-pointer flex xl:hidden text-white rounded-md transition-all font-bold"
            onClick={() => alert('Coming Soon')}
          >
            Join our Discord
          </a>
          <button
            path="/"
            className="flex xl:hidden text-white border-2 border-white py-2 px-4 rounded-md hover:bg-white hover:text-black transition-all font-bold"
            onClick={() => alert('Coming Soon')}
          >
            Connect Wallet
          </button>
        </div>
      </nav>
    </>
  );
};

export default Navbar;