import React, { Component } from 'react'
import Slider from 'react-slick'

import { IconContext } from 'react-icons';

import * as AiIcons from 'react-icons/ai';

import news1 from '../../assets/images/news/news1.png';
import news2 from '../../assets/images/news/news2.png';
import news3 from '../../assets/images/news/news3.png';
import news4 from '../../assets/images/news/news4.png';
import news from '../../assets/images/news/news.jpg';
import garisTitle from '../../assets/icons/garisTitle.svg';
import leftArrow from '../../assets/icons/leftArrow.svg';
import rightArrow from '../../assets/icons/rightArrow.svg';

const newsData = [
  {
    id: 1,
    name: 'Banner 1',
    position: 'test',
    img: news,
  },
  {
    id: 2,
    name: 'Banner 2',
    position: 'test',
    img: news,
  },
  {
    id: 3,
    name: 'Banner 3',
    position: 'test',
    img: news,
  },
  {
    id: 4,
    name: 'Banner 4',
    position: 'test',
    img: news,
  },
  {
    id: 4,
    name: 'Banner 4',
    position: 'test',
    img: news,
  },
];

export default class News extends Component {
   constructor(props) {
    super(props);
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }

  render() {
    const settingsNews = {
      arrows: false,
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 1,
      responsive: [
        {
          breakpoint: 1028,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 1,
            infinite: false,
            dots: true,
            autoplay: true,
            speed: 700,
          },
        },
        {
          breakpoint: 810,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
            autoplay: true,
            speed: 700,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay: true,
            speed: 700,
          },
        },
      ]
    };
    return (
      <section id="teams" className="container mx-auto mt-40">
        <div className="flex justify-between items-center">
          <div className="left flex items-center">
            <h2 className="text-white font-black text-xl mb-14">Latest News</h2>
            <img src={garisTitle} className='-mt-12 ml-6' alt="" />
          </div>
        </div>
        <div className='flex justify-end gap-4 mb-8'>
          <button className="button" onClick={this.previous}>
            <img src={leftArrow} className='' alt=""  />
          </button>
          <button className="button" onClick={this.next}>
            <img src={rightArrow} className='' alt=""  />
          </button>
        </div>
        <Slider ref={c => (this.slider = c)} {...settingsNews}>
          {newsData.map((item) => (
            <div key={item.id} className="h-28">
              <div className="h-48 flex justify-center">
                <div className="img-wrap h-full mr-4 ml-4 overflow-hidden rounded-md w-full">
                  <img
                    src={item.img}
                    alt={item.name}
                    height="100%"
                    width="100%"
                    className="object-cover rounded-md hover:border-1 hover:border-white"
                  />
                </div>
              </div>
            </div>
          ))}
        </Slider>
      </section>
    );
  }
}
