const Home = () => {
  const [isAlert, setIsAlert] = useState(false);
  const [dataWallet, setDataWallet] = useContext(WalletContext);

  const init = async () => {
    if (window.ethereum === undefined && window.screen.width > 768) {
      setIsAlert(true);
    }
    let akun, bal;
    const isChace = await localStorage.getItem('WEB3_CONNECT_CACHED_PROVIDER');
    if (isChace !== null) {
      web3.eth.getAccounts(async (err, acc) => {
        if (acc.length !== 0) {
          akun = acc[0];
          await web3.eth.getBalance(akun).then((balance) => {
            const num = Number(web3.utils.fromWei(balance, 'ether'));
            bal = num.toFixed(4);
          });
        }
        await setDataWallet({ ...dataWallet, account: akun, balance: bal });
      });
    }
  };

  useEffect(() => {
    init();
  }, []);

  return (
    <div id="Home" className="relative">
      {/* <Alert
        title="No wallet extension installed. Try Metamask"
        link="here"
        isAlert={isAlert}
        setIsAlert={setIsAlert}
      /> */}
      {/* <Cover data={imageCover} /> */}
      <Navbar />
      <main>
        {/* <Hero /> */}
        <Wakaspacies />
        <About />
        <Project />
        <Collections />
        <Features />
        <Tokenomics/>
        <Roadmap />
        <Teams />
        <Investor/>
      </main>
      <Footer />
    </div>
  );
};

export default Home;
