import React from 'react'

export default function ButtonWhite() {
  return (
    <>
      <button
        path="/"
        className="bg-white text-black border-2 border-white my-6 py-2 px-8 rounded-md hover:bg-black hover:text-white transition-all font-bold  lg:px-16 sm:text-base sm:px-8 sm:py-3 mr-10 md:mx-auto"
        onClick={() => alert('Coming Soon')}
      >
        Get Started
      </button>
    </>
  )
}
