import React from 'react';

import telegram from '../../assets/social/tele.svg';
import twitter from '../../assets/social/twitter.svg';
import medium from '../../assets/social/medium.png';
import reddit from '../../assets/social/reddit.svg';
import discord from '../../assets/social/discord.svg';
import ig from '../../assets/social/ig.png';
import youtube from '../../assets/social/youtube.svg';

const dataIcon = [
  {
    id: 1,
    name: 'discord',
    path: 'https://discord.gg/5GDdDdTexu',
    imgURL: discord,
  },
  {
    id: 2,
    name: 'twitter',
    path: 'https://twitter.com/WakaSpace',
    imgURL: twitter,
  },
  {
    id: 3,
    name: 'telegram',
    path: 'https://t.me/wakaspace',
    imgURL: telegram,
  },
  {
    id: 4,
    name: 'reddit',
    path: 'https://www.reddit.com/u/wakaspace',
    imgURL: reddit,
  },
  {
    id: 5,
    name: 'ig',
    path: 'https://instagram.com/wakaspace_official',
    imgURL: ig,
  },
  {
    id: 6,
    name: 'medium',
    path: 'https://wakaspace.medium.com',
    imgURL: medium,
  },
  {
    id: 7,
    name: 'youtube',
    path: 'https://www.youtube.com/channel/UCLgexszkoW-toiJAwovHh7Q',
    imgURL: youtube,
  },
];

const SocialHeader = () => {
  return (
    <>
      {dataIcon
        .filter((item) => item.id <= 4)
        .map((item) => (
          <a
            key={item.id}
            href={item.path}
            target="_blank"
            rel="noopener noreferrer"
            className="hover:opacity-50 transition-all w-7 lg:w-12 sm:w-8"
          >
            <img
              src={item.imgURL}
              alt={item.name}
              className="w-full h-full object-cover"
            />
          </a>
        ))}
    </>
  );
};
const SocialFooter = () => {
  return (
    <>
      {dataIcon.map((item) => (
        <a
          key={item.id}
          href={item.path}
          target="_blank "
          rel="noopener noreferrer"
          className="hover:opacity-50 transition-all w-7 lg:w-12 sm:w-8"
        >
          <img
            src={item.imgURL}
            alt={item.name}
            className="w-full h-full object-cover"
          />
        </a>
      ))}
    </>
  );
};

const Social = (props) => {
  const className = [props.className];
  const headerOrFooter = () => {
    if (props.isHeader) {
      return <SocialHeader />;
    } else if (props.isFooter) {
      return <SocialFooter />;
    }
  };
  return (
    <section id="social_icon" className={`${className.join(' ')}`}>
      {headerOrFooter()}
    </section>
  );
};

export default Social;
