/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';

const Count = () => {
  const [timerDays, setTimerDays] = useState('..');
  const [timerHours, setTimerHours] = useState('..');
  const [timerMinutes, setTimerMinutes] = useState('..');
  const [timerSeconds, setTimerSeconds] = useState('..');

  let interval;

  function formatTime(time) {
    return time < 10 ? `0${time}` : time;
  }

  const startTimer = () => {
    const countDownDate = new Date('26 Dec 2021 09:00:00').getTime();

    interval = setInterval(() => {
      const now = new Date().getTime();

      const distance = countDownDate - now;

      const days = Math.floor(distance / (24 * 60 * 60 * 1000));
      const hours = Math.floor(
        (distance % (24 * 60 * 60 * 1000)) / (1000 * 60 * 60)
      );
      const minutes = Math.floor((distance % (60 * 60 * 1000)) / (1000 * 60));
      const seconds = Math.floor((distance % (60 * 1000)) / 1000);

      if (distance < 0) {
        // Stop Timer
        clearInterval(interval.current);
      } else {
        // Update Timer
        setTimerDays(formatTime(days));
        setTimerHours(formatTime(hours));
        setTimerMinutes(formatTime(minutes));
        setTimerSeconds(formatTime(seconds));
      }
    });
  };

  useEffect(() => {
    startTimer();
  }, []);

  return (
    <div>
      <div
        className="count-container grid grid-flow-col gap-3 w-max"
        style={{ gridGap: '12px' }}
      >
        <div className="flex flex-col">
          <span className="font-bold text-40pix leading-44pix px-1 bg-white rounded-radius5">
            {timerDays}
          </span>
          <span className="text-white font-light text-xs mt-1">days</span>
        </div>
        <div className="flex flex-col">
          <span className="font-bold text-40pix leading-44pix px-1 bg-white rounded-radius5">
            {timerHours}
          </span>
          <span className="text-white font-light text-xs mt-1">hours</span>
        </div>
        <div className="flex flex-col">
          <span className="font-bold text-40pix leading-44pix px-1 bg-white rounded-radius5">
            {timerMinutes}
          </span>
          <span className="text-white font-light text-xs mt-1">minutes</span>
        </div>
        <div className="flex flex-col">
          <span className="font-bold text-40pix leading-44pix px-1 bg-white rounded-radius5">
            {timerSeconds}
          </span>
          <span className="text-white font-light text-xs mt-1">seconds</span>
        </div>
      </div>
    </div>
  );
};

Count.defaultProps = {
  timerDays: 10,
  timerHours: 10,
  timerMinutes: 10,
  timerSeconds: 10,
};

export default Count;
