import React from 'react'
import wakaspaciesText from '../../assets/textImg/wakaspaciesText.svg'
import garisTitle from '../../assets/icons/garisTitle.svg';
import twitter from '../../assets/social/twitter.svg'
import tele from '../../assets/social/tele.svg'
import discord from '../../assets/social/discord.svg'

export default function Wakaspacies() {
  return (
      <section className="container mx-auto pt-40 pb-20">
          <div className="flex items-center justify-center text-center flex-col">
            <img src={wakaspaciesText} alt="" className='-mb-20 sm:w-5/6 sm:-mb-0' />
            <h1 className="text-white md:text-2xl text-4xl mt-16 md:mt-0 font-bold uppercase w-8/12 tracking-wide lg:w-7/12 md:w-8/12 sm:w-full akira">
            We are Wakaspacies
        </h1>
            <img src={garisTitle} className='' alt="" />
            <div className="flex items-center gap-x-60 gap-y-40 mt-32 flex-wrap justify-center">
              <div className="left w-18 flex flex-col items-center text-center">
                <img src={twitter} alt="" className='w-3/6' />
                <div className="text mt-6">
                  <h3 className="text-white text-5xl akira uppercase">5.4 k</h3>
                  <h3 className="text-white text-md akira uppercase">followers</h3>
                </div>
              </div>
              <div className="center w-18 flex flex-col items-center text-center">
                <img src={tele} alt="" className='w-3/6' />
                <div className="text mt-6">
                  <h3 className="text-white text-5xl akira uppercase">14 k</h3>
                  <h3 className="text-white text-md akira uppercase">members</h3>
                </div>
              </div>
              <div className="right w-18 flex flex-col items-center text-center">
                <img src={discord} alt="" className='w-3/6' />
                <div className="text mt-6">
                  <h3 className="text-white text-5xl akira uppercase">1.2 k</h3>
                  <h3 className="text-white text-md akira uppercase">members</h3>
                </div>
              </div>
            </div>
          </div>
      </section>
  )
}
