import React, { Component, useState } from 'react'
import "./index.css";
import Slider from "react-slick";
import imgProject1 from '../../assets/images/project/imgProject1.svg';
import { FaArrowRight, FaArrowLeft } from "react-icons/fa";
import { BsFillArrowLeftSquareFill, BsFillArrowRightSquareFill } from "react-icons/bs";
import CardProject from '../../components/CardProject';
import FieldText from '../../components/FieldText';
import imgRPG from '../../assets/images/rpg.png';
import project2 from '../../assets/images/project2.png';
import project3 from '../../assets/images/project3.png';
import garisTitle from '../../assets/icons/garisTitle.svg';
import iconProject from '../../assets/icons/iconProject.svg';
import leftArrow from '../../assets/icons/leftArrow.svg';
import rightArrow from '../../assets/icons/rightArrow.svg';
import projectFont from '../../assets/images/project/projectFont.svg';

const images = [imgProject1, imgProject1, imgProject1, imgProject1];

function Project() {
  const NextArrow = ({ onClick }) => {
    return (
      <div className="arrow next" onClick={onClick}>
        <img src={rightArrow} alt="" />
      </div>
    );
  };

  const PrevArrow = ({ onClick }) => {
    return (
      <div className="arrow prev" onClick={onClick}>
        <img src={leftArrow} alt="" />
      </div>
    );
  };

  const [imageIndex, setImageIndex] = useState(0);

  const settings = {
    infinite: true,
    arrows: true,
    dots: true,
    dotsClass: "button__bar",
    lazyLoad: true,
    speed: 300,
    slidesToShow: 2,
    leftMode: true,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    beforeChange: (current, next) => setImageIndex(next),
    responsive: [
        {
          breakpoint: 1028,
          settings: {
            slidesToShow: 2,
          },
        },
        {
          breakpoint: 810,
          settings: {
            slidesToShow: 1,
            speed: 700,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
          },
        },
      ]
    };
    return (
      <section
      id="Project"
      className="overflow-hidden mt-48 lg:mt-0 md:mt-20 sm:mt-0"
    >
      <div className="container mx-auto">
        <div className="left flex items-center">
          <h2 className="text-white font-black text-xl mb-14">Project</h2>
          <img src={garisTitle} className='-mt-12 ml-6' alt="" />
        </div>
        <div className="flex justify-between items-center">
          <div className="flex items-center gap-4 sm:flex-col sm:items-start">
            <img src={iconProject} alt="" />
            <h2 className="text-2xl text-white font-semibold w-60">Wakaspace NFT Marketplace</h2>
          </div>
        </div>
        <Slider {...settings}>
          {images.map((img, idx) => (
            <div className={idx === imageIndex ? "slide activeSlide" : "slide"}>
              <img src={img} alt={img} />
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
}

export default Project;